import ListItem from '../../../components/ListItem/ListItem.vue'

import UserModel from '../../../models/user.js';
import PostModel from '@/models/job.js'
const postModel = new PostModel()
const userModel = new UserModel() 
export default {
	data(){
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			jobList: [],
			pageNum: 1,
			pageSize: 10,
			label: '',
			loading: false, 
			hasEnd: false,
		}
	},
	onReady() {
		this.getPublishJobList()
	}, 
	components:{
		ListItem
	},
	methods:{
		showDetail(id){
			uni.navigateTo({
				url: '../../PostDetail/PostDetail?id='+id
			})
		},

		getUserInfo(e){
			console.log(e)
		},
		switchToCustomer(){
			uni.reLaunch({
				url: '../../customer/main/main'
			})
		},
		getPublishJobList(){
			postModel.publishJobList(this.pageNum, this.pageSize, this.status).then(res=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				const { code, message, data } = res.data
				if(code === '0'){
					if(this.pageNum === 1){
						that.jobList = data
					}else {
						that.jobList = [...that.jobList, ...data]
					}
					if(data.length < that.pageSize){
						that.hasEnd = true
					}
				}else {
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				uni.showToast({
					icon: 'none',
					title:'获取项目信息失败'
				})
			})
		},
		refresh(){
			this.pageNum = 1
			this.hasEnd = false
			this.label = ''
			this.getPublishJobList()
		},
		getNext(){
			if(!this.hasEnd){
				this.pageNum += 1
				this.getPublishJobList()
			}
		},
		switchLabel(label){
			this.label = label
			this.pageNum = 1
			this.hasEnd = false
			this.getJobList()
		}
	}
}
